body {
    margin: 0;
    padding: 0;
}

.PostWrapper {
    width: 50rem;
    margin: 0 auto;
}

@media (max-width: 768px) {
    .PostWrapper {
        width: 90%;    
    }
}
.HeaderBox {
    background-color: #333333;
    color: white;
    font-size: 1.2rem;
    text-align: center;
    font-weight: 600;
    height:48px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;    

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 97;
    background: #313131;
}

.HeaderL {
    min-width: 80px;
    margin-left:1em;
}

.HeaderC {
    -webkit-flex: 1 1 auto;
    flex: 1 1 auto;
}

.HeaderR {
    min-width: 80px;
    margin-right:1em;
}
.Footer {
    background-color: #f5f7fa;
    margin-top:2em;
    padding-top:1.2em;
    padding-bottom:1.2em;
}

.FooterHeading {
    margin: 0.5em 0em;
    text-align: center;
}

.CompanyName {
    font-size: 1em;
    color:#888;
    margin:1.5em;
}

.BizDesc {
    font-size: 0.88em;
    color:#AAA;
    text-align:left;
}

.Footer .item:first {
    border-left:0;
}

.Footer .item:last {
    border-right:0;
}
.ShopItem {
}

.ShopItem > .item {
  border-top:1px solid #EEE;
  padding-top:15px;
  height:80px;
}

.ItemIcon {
  width:50px;
  height:50px;
}

.PurchaseBox {
  padding-top: 22px;
}

.ItemTitleBox {
  padding:20px 0px;
}

.ItemTitle {
  font-size: 0.85em;
  font-weight: 600;
}

.WonPrice {
  font-size: 0.85em;
  font-weight: 600;
  color: #2185d0;
}

.KlayPrice {
  font-size: 0.85em;
  color: #888;
}
.AccountBox {
  margin-top:70px;
  margin-bottom:1em;
}

.AccountBox h3 {
  font-size:1.2em;
  margin:0;
  padding:0;
}

.AccountBox .Desc {
  font-size:0.9em;
  margin:0;
  padding:0;
  color:#AAA;
}

.AccountBox .AppIcon {
  width: 120px;
  height: auto;
}

.AccountBox .AppLinkBox {
  clear: both;
  margin-top:0.3em;
}

.AccountBox .address {
  font-size:0.8em;
  text-decoration: underline;
}
.GameInfoBox div {
  margin-top:1.5em;
  margin-bottom:1em;
}

.GameInfoBox h3 {
  font-size:1.2em;
  margin:0;
  padding:0;
}

.GameInfoBox .Desc {
  font-size:0.9em;
  margin:0;
  padding:0;
  color:#AAA;
}

.GameInfoBox .AppIcon {
  width: 120px;
  height: auto;
}

.GameInfoBox .AppLinkBox {
  clear: both;
  margin-top:0.3em;
}
.SimpleLoader {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgb(0, 0, 0);
  opacity: 0.2;
}

.App {
  text-align: center;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
